body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 10vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 20vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header-div {
  font: inherit;
}

.App-header-div-logo {
  float: left;
}

.App-header-div-title {
  float: left;
}

.App-header-div-title h1 {
  margin: 0px;
  vertical-align: middle;
}

.App-link {
  color: #61dafb;
}

section.AppSection {
  margin: 0px 20px 20px 0px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

div.pharma-person {
  text-align: justify;
  padding-left: 1em;
}

.pharma-control-title{
  font-weight: bold;
}

.pharma-btn {
  display: inline-block;
  border: none;
  border-radius: 10px;
  padding: 0.2em 0.4em;
  margin: 0;
  text-decoration: none;
  background: #0069ed;
  color: #ffffff;
  font-family: sans-serif;
  font-size: 1rem;
  cursor: pointer;
  text-align: center;
  transition: background 250ms ease-in-out, 
              -webkit-transform 150ms ease;
  transition: background 250ms ease-in-out, 
              transform 150ms ease;
  transition: background 250ms ease-in-out, 
              transform 150ms ease, 
              -webkit-transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
  -webkit-filter: saturate(0.70);
          filter: saturate(0.70);
}

.pharma-btn:hover,
.pharma-btn:focus {
    /*background: #0053ba;
    color: white;*/
    -webkit-filter: saturate(0.90);
            filter: saturate(0.90);
}

.pharma-btn:focus {
  outline: 1px solid #fff;
  outline-offset: -4px;
}

.pharma-btn:active {
  -webkit-transform: scale(0.99);
          transform: scale(0.99);
  -webkit-filter: saturate(0.99);
          filter: saturate(0.99);
}

.pharma-btn-add {
  background-color: violet;
  color: black;
}

.pharma-btn-edit {
  background-color: aqua;
  color: black;
}

.pharma-btn-update {
  background-color: greenyellow;
  color: black;
}

.pharma-btn-cancel {
  background-color: orange;
  color: black;
}

.pharma-btn-delete {
  background-color: coral;
  color: black;
}

.App-footer {
  background-color: silver;
  min-height: 3vh;
  max-height: 5vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(6px + 2vmin);
  color: black;
  font-style: italic;
}

.App-footer-version{
  display:inline;
  margin: 0px 5px 0px 5px;
}

.App-footer-copyright{
  display:inline;
  margin: 0px 5px 0px 5px;
}
